import {
    GET_GENRES,
    GET_SINGERS,
    SET_AUDIO_REF,
    SET_MUSIC,
} from "../types/types";

const initialState = {
    genres: null,
    singers: null,
    music: null,
    audioRef: false,
};

export default function main(state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case GET_GENRES:
            return {
                ...state,
                genres: payload,
            };
        case GET_SINGERS:
            return {
                ...state,
                singers: payload,
            };
        case SET_MUSIC:
            return {
                ...state,
                music: payload,
            };
        case SET_AUDIO_REF:
            return {
                ...state,
                audioRef: payload,
            };
        default:
            return state;
    }
}
