import { GET_GENRES, GET_SINGERS } from "../types/types";

// import errors from "./../messages/errors.json";
// import success from "./../messages/success.json";
import axios from "axios";

export const get_genres = () => async (dispatch) => {
    const config = {
        headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
        },
    };
    try {
        const res = await axios.get(
            `${process.env.REACT_APP_API_VERSION_URL}/spotify/genres/`,
            config
        );
        dispatch({
            type: GET_GENRES,
            payload: res.data,
        });
    } catch (error) {
        // console.log("Not authenticated");
    }
};
