export const get_genre = (slug, id) => async (dispatch) => {
    const config = {
        headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
        },
    };
    try {
        const res = await axios.get(
            `${process.env.REACT_APP_API_VERSION_URL}/spotify/genres/${id}`,
            config
        );
        return res.data;
    } catch (error) {
        // console.log("Not authenticated");
    }
};
