import React, {
    useState,
    isValidElement,
    Children,
    cloneElement,
    useEffect,
} from "react";
import { useDispatch } from "react-redux";
import Footer from "../components/Footer";
import HomeHeader from "../components/HomeHeader";
import Sidebar from "../components/Sidebar";
import { get_genres, get_singers } from "../redux/actions/main";

function MainLayout({ children }) {
    const dispatch = useDispatch();
    const [open, setOpen] = useState(false);
    const childrenWithProps = Children.map(children, (child) => {
        // Checking isValidElement is the safe way and avoids a typescript
        if (isValidElement(child)) {
            return cloneElement(child, { open, setOpen });
        }
        return child;
    });
    useEffect(() => {
        dispatch(get_genres());
        dispatch(get_singers());
    }, []);
    document.body.style.background = "#121212";
    return (
        <>
            <Sidebar open={open} />
            <section
                class={` text-white my-container  ${open && "active-cont"}`}
                style={{ background: "#121212", paddingBottom: "90px" }}
            >
                <HomeHeader open={open} setOpen={setOpen} />
                {childrenWithProps}
            </section>
            <Footer />
        </>
    );
}

export default MainLayout;
