import React, { useEffect } from "react";

// Redux
import { useDispatch, useSelector } from "react-redux";
import { checkAuthenticated, load_user } from "../redux/actions/auth";
import {
	selectErrors,
	selectSuccessMessages,
} from "../redux/selectors/messages";
import { REMOVE_ERRORS, REMOVE_MESSAGES } from "../redux/types/types.js";

const mapState = (state) => ({
	errors: selectErrors(state),
	successMessages: selectSuccessMessages(state),
});

function MessagesLayout(props) {
	// const { t, i18n } = useTranslation(["errors", "success"]);
	const dispatch = useDispatch();
	const { errors, successMessages } = useSelector(mapState);
	useEffect(() => {
		dispatch(checkAuthenticated());
		dispatch(load_user());
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	return (
		<>
			{errors && (
				<div
					class="alert sticky-top mx-auto  py-3 alert-dismissible fade show alert-danger"
					role="alert"
					style={{ borderRadius: "10px", top: "5px", width: "95%" }}
				>
					<div className="container d-flex justify-content-between align-items-center">
						<div className="ps-2 d-inline-flex align-items-center">
							<i class="bi bi-exclamation-triangle-fill me-2"></i>

							<strong>{errors}</strong>
							{/* <strong>{t(`errors:${errors}`)}</strong> */}
						</div>
						<button
							type="button"
							aria-label="Remove errors"
							class="close-btn btn"
							onClick={() => dispatch({ type: REMOVE_ERRORS })}
						>
							<i class="bi bi-x-lg text-dark"></i>
						</button>
					</div>
				</div>
			)}

			{successMessages && (
				<div
					class={`alert sticky-top py-3  alert-dismissible fade show alert-success`}
					role="alert"
					style={{
						borderRadius: "10px",
						top: "5px",
					}}
				>
					<div className="container d-flex justify-content-between align-items-center">
						<div className="ps-2 d-inline-flex align-items-center">
							<i class="bi bi-check-circle-fill me-2"></i>

							<strong>{successMessages}</strong>
							{/* <strong>{t(`success:${successMessages}`)}</strong> */}
						</div>
						<button
							type="button"
							aria-label="Remove Messages"
							class="close-btn btn"
							onClick={() => dispatch({ type: REMOVE_MESSAGES })}
						>
							<i class="bi bi-x-lg text-dark"></i>
						</button>
					</div>
				</div>
			)}

			<>{props.children}</>
		</>
	);
}

export default MessagesLayout;
