import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

const mapState = ({ main }) => ({
    genres: main.genres,
    singers: main.singers,
});

function Sidebar({ open }) {
    const [dropdow, setDropdow] = useState({
        genresDrop: false,
        singersDrop: false,
    });
    const { genres, singers } = useSelector(mapState);
    return (
        <nav
            className={`navbar bg-black navbar-expand d-flex flex-column align-items-start ${
                open && "active-nav"
            }`}
            style={{
                zIndex: 1000,
                position: "absolute",
                paddingBottom: "100px",
            }}
        >
            <Link to="/" className="mx-auto my-3">
                <img
                    src="https://i.ibb.co/6mGP207/white-logo.png"
                    alt="logo"
                    style={{ width: "200px" }}
                />
            </Link>
            <ul className="list-group my-3 text-white w-100">
                <Link
                    to="/"
                    className="p-2 ps-3 fw-bold mx-2 btn-nav mb-2 text-decoration-none  active d-inline-flex align-items-center "
                >
                    <i class="bi bi-house-door-fill me-3 fs-5"></i>–ì–ª–∞–≤–Ω–∞—è
                </Link>
                <Link
                    to="/search"
                    className="p-2 ps-3 fw-bold mx-2 btn-nav mb-2 text-decoration-none d-inline-flex align-items-center "
                >
                    <i class="bi bi-search me-3 fs-5"></i>–ü–æ–∏—Å–∫
                </Link>
                <Link
                    to="/"
                    className="p-2 ps-3 fw-bold mx-2 btn-nav mb-2 text-decoration-none d-inline-flex align-items-center "
                >
                    <i class="bi bi-collection-play-fill me-3 fs-5"></i>–ú–æ—è
                    –º–µ–¥–∏–∞—Ç–µ–∫–∞
                </Link>
            </ul>
            <ul className="list-group mt-4 text-white w-100">
                <div
                    role="button"
                    onClick={() =>
                        setDropdow({
                            ...dropdow,
                            genresDrop: !dropdow.genresDrop,
                        })
                    }
                    className="p-2 ps-3  fw-bold mx-2 btn-nav mb-1 text-decoration-none  d-inline-flex align-items-center "
                >
                    <i class="bi bi-music-note-list me-3 fs-5"></i>
                    –ñ–∞–Ω—Ä—ã
                </div>
                {dropdow.genresDrop ? (
                    <ul className="list-group mt-2 text-white w-100">
                        {Array.isArray(genres) &&
                            genres.length > 1 &&
                            genres.map((genre) => (
                                <Link
                                    to={`/genre/${genre.slug}/${genre.id}`}
                                    style={{ fontWeight: 500 }}
                                    className="p-2 ps-3 mx-2 btn-nav text-decoration-none  d-inline-flex align-items-center "
                                >
                                    {genre?.name}
                                </Link>
                            ))}
                    </ul>
                ) : (
                    ""
                )}
                <div
                    onClick={() =>
                        setDropdow({
                            ...dropdow,
                            singersDrop: !dropdow.singersDrop,
                        })
                    }
                    role="button"
                    className="p-2 ps-3 fw-bold mx-2 btn-nav mb-2 text-decoration-none d-inline-flex align-items-center "
                >
                    <i class="bi bi-people-fill me-3 fs-5"></i>
                    –ü–µ–≤—Ü—ã
                </div>
                {dropdow.singersDrop ? (
                    <ul className="list-group mt-2 text-white w-100">
                        {Array.isArray(singers) &&
                            singers.length >= 1 &&
                            singers.map((singer) => (
                                <Link
                                    to={`/singer/${singer.slug}/${singer.id}`}
                                    style={{ fontWeight: 500 }}
                                    className="p-2 ps-3 mx-2 btn-nav text-decoration-none  d-inline-flex align-items-center "
                                >
                                    {singer?.name}
                                </Link>
                            ))}
                    </ul>
                ) : (
                    ""
                )}
            </ul>
            <hr className="bg-white mx-auto" style={{ width: "90%" }} />
        </nav>
    );
}

export default Sidebar;
