import React, { Suspense } from "react";

// Router
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
// Redux
import MainLayout from "./layouts/MainLayout";

// Css
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-icons/font/bootstrap-icons.css";
import "./css/custom.css";

import { authRoutes, mainRoutes } from "./routes";
import WithLoged from "./hocs/other/withLoged";
import MessagesLayout from "./layouts/MessagesLayout";
import AuthLayout from "./layouts/AuthLayout";

function App() {
    return (
        <MessagesLayout>
            <Suspense fallback={<div>Loading...</div>}>
                {/* For Github pages route 
                NOTE: Remove it if you deploy to another host service */}
                <Router basename={"/spotifytm"}>
                    <Switch>
                        {authRoutes.map((route) => (
                            <Route {...route}>
                                <WithLoged>
                                    <AuthLayout>{route.comp}</AuthLayout>
                                </WithLoged>
                            </Route>
                        ))}
                        {mainRoutes.map((route) => (
                            <Route {...route}>
                                {/* <WithAuth> */}
                                <MainLayout>{route.comp}</MainLayout>
                                {/* </WithAuth> */}
                            </Route>
                        ))}
                        <Route
                            path="/admin"
                            component={() => {
                                window.location.href =
                                    "https://spotifytm.pythonanywhere.com/admin";
                                return null;
                            }}
                        />
                    </Switch>
                </Router>
            </Suspense>
        </MessagesLayout>
    );
}

export default App;
