import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { selectMusic } from "../redux/selectors/music";
import { SET_AUDIO_REF } from "../redux/types/types";
import { secondsToHms } from "../utils/secondsToHms";
import "./footer.css";

function Footer() {
    const music = useSelector((state) => selectMusic(state));
    const [persantage, setPersentage] = useState(0);
    const [volume, setVolume] = useState(0);
    const audioRef = useRef();
    const dispatch = useDispatch();
    const [isPlaying, setIsPlaying] = useState(false);
    const [duration, setDuration] = useState(0);
    const [currentTime, setCurrentTime] = useState(0);
    const [muted, setMuted] = useState(false);
    const play = () => {
        // Start Music
        const audio = audioRef.current;
        setIsPlaying(true);
        audio.play();
    };
    const pause = () => {
        // Pause
        const audio = audioRef.current;
        setIsPlaying(false);
        audio.pause();
    };
    const backRemote = (e) => {
        // Back for 10s
        const audio = audioRef.current;
        console.log("CURRENT TIME", audio.currentTime);
        audio.currentTime = audio.currentTime - 10;
        console.log("CURRENT TIME BEFORE 10s", audio.currentTime);
    };
    const forwardRemote = (e) => {
        // Next for 10s
        const audio = audioRef.current;
        console.log("CURRENT TIME", audio.currentTime);
        audio.currentTime = audio.currentTime + 10;
        console.log("CURRENT TIME IN 10s", audio.currentTime);
    };

    const getCurrDuration = (e) => {
        // Get how much time is passed
        const percent = (
            (e.currentTarget.currentTime / e.currentTarget.duration) *
            100
        ).toFixed(2);
        const time = e.currentTarget.currentTime; /// 60
        setPersentage(+percent);
        setCurrentTime(time.toFixed(2));
    };

    const mute = (e) => {
        const audio = audioRef.current;
        if (audio.muted == true) {
            audio.muted = false;
            setMuted(false);
        } else {
            audio.muted = true;
            setMuted(true);
        }
    };

    useEffect(() => {
        dispatch({ type: SET_AUDIO_REF, payload: audioRef });
    }, [audioRef]);

    useEffect(() => {
        // Starting if music changes
        if (music) {
            play();
        }
    }, [music]);

    return (
        <>
            <div
                style={{ zIndex: 2000 }}
                className="footer m-0  text-white w-100 position-fixed "
            >
                <div
                    className="slider-cont position-absolute "
                    style={{ top: "-15px", width: "100%" }}
                >
                    <div
                        className="progress-bar-cover"
                        style={{ width: `${persantage}% ` }}
                    ></div>
                    <div
                        className="thumb"
                        style={{
                            left: `${persantage}%`,
                            marginLeft: `${(20 / 100) * persantage * -1}px`,
                        }}
                    ></div>
                    <input
                        type="range"
                        onChange={(e) => {
                            const audio = audioRef.current;
                            if (audio?.currentTime) {
                                audio.currentTime =
                                    (audio.duration / 100) * e.target.value;
                                setPersentage(e.target.value);
                            }
                        }}
                        step="0.01"
                        value={persantage}
                        className="music-slider"
                        name="music-slider"
                        id="first-music-slider"
                    />
                </div>
                <div className="align-items-center  row justify-content-between p-2">
                    <div className="footer__left ms-2 d-none align-items-center d-md-flex col-3">
                        <img
                            src="https://i.scdn.co/image/ab67616d00004851c5909eb7b91b1e9a0a3a74bf"
                            style={{
                                width: "40px",
                                borderRadius: "3px",
                                height: "40px",
                            }}
                        />
                        <div className="footer__songInfo ms-3">
                            <strong className="fs-5">{music?.name}</strong>
                        </div>
                    </div>
                    <div className="footer__center fs-4 col-md-4 mx-auto text-white d-flex align-items-center justify-content-between">
                        <div className="fs-6">{secondsToHms(currentTime)}</div>
                        <i
                            onClick={backRemote}
                            role="button"
                            class="bi bi-skip-start-fill footer__icon"
                        ></i>
                        {isPlaying ? (
                            <i
                                role="button"
                                class="bi bi-pause-circle-fill fs-1 footer__icon"
                                onClick={() => pause()}
                            ></i>
                        ) : (
                            <i
                                role="button"
                                onClick={() => play()}
                                class="bi bi-play-circle fs-1 footer__icon"
                            ></i>
                        )}
                        <i
                            role="button"
                            class="bi bi-skip-end-fill footer__icon"
                            onClick={forwardRemote}
                        ></i>
                        <div className="fs-6">{secondsToHms(duration)}</div>
                    </div>
                    <div className="footer__right d-none col-3 d-md-flex align-items-center justify-content-between">
                        <div className="col-auto mx-2">
                            {!muted ? (
                                <i
                                    class="bi bi-volume-up-fill fs-4"
                                    onClick={mute}
                                ></i>
                            ) : (
                                <i
                                    class="bi bi-volume-mute-fill fs-4"
                                    onClick={mute}
                                ></i>
                            )}
                        </div>

                        <div className="col volume-container ">
                            <input
                                type="range"
                                onChange={(e) => {
                                    const audio = audioRef.current;

                                    audio.volume = e.target.value / 100;
                                    setVolume(e.target.value);
                                }}
                                value={volume}
                                step="0.01"
                                className="w-100 volume-slider"
                                name="volume-slider"
                                id="second-music-slider"
                            />
                        </div>
                    </div>
                </div>
                <audio
                    ref={audioRef}
                    src={music?.song_file}
                    onLoadedData={(e) => {
                        // 1 100%
                        // e x%
                        setDuration(e.currentTarget.duration.toFixed(2));
                        setVolume(e.currentTarget.volume * 100);
                    }}
                    onTimeUpdate={getCurrDuration}
                ></audio>
            </div>
        </>
    );
}

export default Footer;

