import {
	LOGIN_SUCCESS,
	LOGIN_FAIL,
	SIGN_UP_FAIL,
	SIGN_UP_SUCCESS,
	ACTIVATION_FAIL,
	ACTIVATION_SUCCESS,
	USER_LOADED_SUCCESS,
	AUTHENTICATED_SUCCESS,
	LOGOUT,
	ADD_MESSAGE,
	ADD_ERROR,
	LOAD_ACCOUNT_DATA,
} from "../types/types";

import errors from "./../messages/errors.json";
import success from "./../messages/success.json";
import axios from "axios";

// ===============Load=User====================================
// Taking an access token and trying to retrieve user information

export const load_user = () => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};
		try {
			const res = await axios.get(
				`${process.env.REACT_APP_API_URL}/auth/users/me/`,
				config
			);
			dispatch({
				type: USER_LOADED_SUCCESS,
				payload: res.data,
			});
			// console.log("Fetched success: ", res.data);
		} catch (error) {
			// console.log("Not authenticated");
		}
	} else {
		// console.log("Not logged");
	}
};

// ===============Login====================================
// Taking credentials and send it to take the jwt token

export const login = (username, password) => async (dispatch) => {
	const config = {
		headers: {
			"Content-Type": "application/json",
		},
	};

	const body = JSON.stringify({ username, password });
	console.log("Started login", body);
	console.log("URL", process.env.REACT_APP_API_URL);
	try {
		const res = await axios.post(
			`${process.env.REACT_APP_API_URL}/auth/jwt/create/`,
			body,
			config
		);
		// console.log("Logged successfully")
		dispatch({
			type: LOGIN_SUCCESS,
			payload: res.data,
			message: success.success3,
		});

		dispatch(load_user());
	} catch (error) {
		// console.error("Loggin error 1", error.message);
		dispatch({
			type: LOGIN_FAIL,
			payload: errors.error4,
		});
	}
};
// ===============Get access token====================================
// Taking credentials and send it to take the jwt token

export const get_access_token = () => async (dispatch) => {
	const config = {
		headers: {
			"Content-Type": "application/json",
		},
	};

	const body = JSON.stringify({ refresh: localStorage.getItem("refresh") });
	console.log("Started refreshing token");
	try {
		const res = await axios.post(
			`${process.env.REACT_APP_API_URL}/auth/jwt/refresh/`,
			body,
			config
		);
		// console.log("Logged successfully")
		dispatch({
			type: LOGIN_SUCCESS,
			payload: res.data,
		});
	} catch (error) {
		dispatch({
			type: LOGIN_FAIL,
			payload: errors.error4,
		});
	}
};

// ===============Check=Auth====================================
// Taking access token from localstorage and trying to verify

export const checkAuthenticated = () => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};

		const body = JSON.stringify({ token: localStorage.getItem("access") });

		try {
			const res = await axios.post(
				`${process.env.REACT_APP_API_URL}/auth/jwt/verify/`,
				body,
				config
			);

			if (res.data.code !== "token_not_valid") {
				// console.log("Checked successfully")
				dispatch({
					type: AUTHENTICATED_SUCCESS,
				});
			} else {
				dispatch(get_access_token());
				// console.log("Token not valid");
			}
		} catch (error) {
			// dispatch(get_access_token());
			// console.log("Old access token");
		}
	} else {
		console.log("No access token");
	}
};

// ===============Logout====================================
// Clearing an access and refresh token from localstorage

export const logout = () => async (dispatch) => {
	// console.log("Started logout");
	dispatch({
		type: LOGOUT,
		payload: success.success4,
	});
};

// ================Reset=Password===================================
// Taking email and sending request to server for resetting password

export const reset_password = (email) => async (dispatch) => {
	const config = {
		headers: {
			"Content-Type": "application/json",
		},
	};
	const body = JSON.stringify({ email });

	try {
		await axios.post(
			`${process.env.REACT_APP_API_URL}/auth/users/reset_password/`,
			body,
			config
		);
		dispatch({
			type: ADD_MESSAGE,
			payload: success.success5,
		});
	} catch (error) {
		dispatch({
			type: ADD_ERROR,
			payload: errors.error3,
		});
	}
};

// ================Reset=Password=Confirm===================================
// Taking uid token and new password and reset password

export const reset_password_confirm = (
	uid,
	token,
	new_password,
	re_new_password
) => async (dispatch) => {
	const config = {
		headers: {
			"Content-Type": "application/json",
		},
	};
	const body = JSON.stringify({ uid, token, new_password, re_new_password });

	try {
		await axios.post(
			`${process.env.REACT_APP_API_URL}/auth/users/reset_password_confirm/`,
			body,
			config
		);

		dispatch({
			type: ADD_MESSAGE,
			payload: success.success6,
		});
	} catch (error) {
		dispatch({
			type: ADD_ERROR,
			payload: errors.error3,
		});
	}
};

// =================Signup==================================
// Creating new user with this credentials

export const signup = (username, email, password, re_password) => async (
	dispatch
) => {
	const config = {
		headers: {
			"Content-Type": "application/json",
		},
	};

	const body = JSON.stringify({
		username,
		email,
		password,
		re_password,
	});
	console.log("Register send >>>", body);
	try {
		await axios.post(
			`${process.env.REACT_APP_API_URL}/auth/users/`,
			body,
			config
		);
		dispatch({
			type: SIGN_UP_SUCCESS,
			payload: success.success7,
		});
		dispatch(login(username, password));
	} catch (error) {
		if (error.response.status === 400) {
			dispatch({
				type: SIGN_UP_FAIL,
				payload: errors.error11,
			});
		} else {
			dispatch({
				type: SIGN_UP_FAIL,
				payload: errors.error5,
			});
		}
	}
};

// ==============Activation=====================================
// Activate and account taking uid and token

export const verify = (uid, token) => async (dispatch) => {
	const config = {
		headers: {
			"Content-Type": "application/json",
		},
	};

	const body = JSON.stringify({ uid, token });
	try {
		await axios.post(
			`${process.env.REACT_APP_API_URL}/auth/users/activation/`,
			body,
			config
		);
		dispatch({
			type: ACTIVATION_SUCCESS,
		});
	} catch (error) {
		dispatch({
			type: ACTIVATION_FAIL,
			payload: errors.error3,
		});
	}
};

export const update_profile = (formInputs, file) => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "multipart/form-data",
				Authorization: `JWT ${localStorage.getItem("access")}`,
			},
		};

		let form_data = new FormData();
		if (file) {
			form_data.append("profile_img", file);
		}
		form_data.append("username", formInputs.username);
		form_data.append("phone_number", formInputs.phoneNumber);
		form_data.append("email", formInputs.email);
		form_data.append("city", formInputs.city);
		form_data.append("address", formInputs.address);
		console.log("Profile data", formInputs);
		console.log("Profile img", file);
		try {
			const res = await axios.patch(
				`${process.env.REACT_APP_API_URL}/auth/users/me/`,
				form_data,
				config
			);
			dispatch(load_user());
			return res;
		} catch (error) {
			return error;
			// console.log(error.message);
		}
	} else {
		// console.log("Not logged");
	}
};

export const get_account_info = () => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};

		try {
			const res = await axios.get(
				`${process.env.REACT_APP_API_VERSION_URL}/account/info/`,
				config
			);
			dispatch({ type: LOAD_ACCOUNT_DATA, payload: res.data });
			return res;
		} catch (error) {
			return errors.error3;
		}
	} else {
		return errors.error1;
	}
};

